.new-collections
{display: flex;
flex-direction: column;
align-items: center;
gap: 10px;
margin-bottom: 100px;}
.new-collections h1
{
    color: rgb(255, 0, 0);
    font-size: 50px;
    font-weight: 600;
}
.new-collections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: rgb(255, 0, 0);
}
.collections
{margin-top: 50px;
display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
gap: 30px;
}